
.movies{
  text-align:center;
  margin-bottom:18px;
}
 .grid{
  margin:20px 0px;
  margin-left:50px;
  display: flex;
  flex-wrap: wrap;
  gap:1.5rem ;
  justify-content: center;
  align-items: center;
 }  
 .my-modal{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
 }
  .ReactModal_Content{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 90%;
  background-color:#06202A;
  max-width: 600px;
  max-height: 100%;
  color: #fff;
  overflow: auto;
  padding: 10px;
  border-radius: 5px;
 } 